import { Row, Col } from "react-bootstrap";
import styles from './Footer.module.scss';
import Copyright from "../Copyright/Copyright";

const Footer = () => {

    return (
        <>
        <div className={styles.main}>
        <Row>
            
                <Col lg={4}>
                    <h1>Informacje</h1>
                        <p>Informacje o sklepie</p>
                        <p>Formy dostawy</p>
                        <p>Opcje patnoci</p>
                        <p>Kody rabatowe</p>
                </Col>

                <Col lg={4}>
                    <h1>Zamwienia</h1>
                        <p>Status zamwienia</p>
                        <p>Reklamacje</p>
                        <p>Zwroty</p>
                        <p>Wymiana</p>
                </Col>

                <Col lg={4}>
                    <h1>Regulamin</h1>
                        <p>Zwroty, reklamacje</p>
                        <p>Regulamin</p>
                        <p>Regulamin newslettera</p>
                        <p>Odstpienia od umowy</p>
                </Col>

            
            </Row>
        </div>
        <Copyright />
        </>
    )
}

export default Footer;